@media all and (max-width: 300px) {
  body {
    font-size: 14px;
  }
  .customer-review-text-block {
    width: 100%;
    height: auto;
  }
}

@media all and (max-width: 500px) {
  .testim .arrow {
    font-size: 1.5em;
  }

  .testim .cont div p {
    line-height: 25px;
  }
  .customer-review-text-block {
    width: 100%;
    height: auto;
  }
}
@media all and (min-width: 500px) and (max-width: 990px) {
  .customer-review-text-block {
    display: flex;
    width: 100%;
    height: 164px;
  }
  .customer-review-text-block svg {
    top: 60px;
  }
}
